import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import Login from '@/pages/login/Login'
import PesRegister from '@/pages/pesRegister/PesRegister'
import EntRegister from '@/pages/entRegister/EntRegister'
import FindPsw from '@/pages/findPsw/FindPsw'
import ResetPsw from '@/pages/resetPsw/ResetPsw'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/home',
      name: 'Home',
      component: Home
    }, {
      path: '/login',
      name: 'Login',
      component: Login
    }, {
      path: '/pesRegister',
      name: 'PesRegister',
      component: PesRegister
    }, {
      path: '/entRegister',
      name: 'EntRegister',
      component: EntRegister
    }, {
      path: '/findPsw',
      name: 'FindPsw',
      component: FindPsw
    }, {
      path: '/resetPsw',
      name: 'ResetPsw',
      component: ResetPsw
    }]
})



