<template>
  <div>
    <div class="main">
      <div class="input-phone">
        <input type="text" placeholder="请输入11位有效手机号" v-model="inputPhone">
      </div>
      <div class="input-yanzheng1">
        <input type="text" placeholder="请获取" v-model="inputYanZheng1">
        <div :class="{ btnBefore:obj.isBtnBefore, btnAfter:obj.isBtnAfter }">
          <button @click="handleClick" :disabled="obj.isClick">{{obj.btInfo}}</button>
        </div>
      </div>
      <div class="input-yanzheng2">
        <input type="text" placeholder="请输入" v-model="inputYanZheng2">
        <div class="input-yanzheng2-img"></div>
      </div>
      <div class="input-psw1">
        <input type="password" placeholder="请输入" v-model="inputPsw1">
      </div>
      <div class="input-psw2">
        <input type="password" placeholder="请输入" v-model="inputPsw2">
      </div>
      <div class="main-btn">
        <button @click="handleSubmit">立即注册</button>
      </div>
      <div class="text-wrapper">
        <div :class="{ cbLabel:iscbLabel, cbLabel2:iscbLabel2 }" @click="handleBoxClick"></div>
        <p class="text">我已同意
          <span class="xieyi">E滁州人才网用户招聘协议</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PesBody",
  data () {
    return {
      inputPhone: '',
      inputYanZheng1: '',
      inputYanZheng2: '',
      inputPsw1: '',
      inputPsw2: '',
      iscbLabel: true,
      iscbLabel2: false,
      obj: {
        btInfo: '获取',
        isClick: false,
        isBtnBefore: true,
        isBtnAfter: false
      }
    }
  },
  methods: {
    handleClick () {
      var obj = this.obj
      var countdown = 60 
		  function settime(obj) {
			  if(countdown == 0) {
				  obj.btInfo  = '获取'
          obj.isClick = false
          obj.isBtnBefore = true
          obj.isBtnAfter = false
				  countdown = 60
				  clearInterval(timer)
				  return;
			  } else {
          obj.isBtnBefore = false
          obj.isBtnAfter = true
          obj.isClick = true
          obj.btInfo = countdown + 's'
          countdown--
        }
		  };
		  var timer = setInterval(function() {
			  settime(obj)
		  }, 1000) 
    },
     handleBoxClick () {
      if (this.iscbLabel) {
        this.iscbLabel = !this.iscbLabel
        this.iscbLabel2 = !this.iscbLabel2
      } else {
        this.iscbLabel = !this.iscbLabel
        this.iscbLabel2 = !this.iscbLabel2 
      }
    },
    handleSubmit () {
      if (this.inputPhone == ''
          || this.inputYanZheng1 == ''
          || this.inputYanZheng2 == '' 
          || this.inputPsw1 == '' 
          || this.inputPsw2 == '') {
        alert('请填写完整信息')
      }
    }
  }
};
</script>

<style scoped>
.main {
  padding: 0 .48rem;
}
.main .main-title {
  font-size: 0;
  font-family: PingFangSC-Regular;
  color: #979799;
  text-align: center;
}
.main .main-title .title-left {
  font-size: .3rem;
  display: inline-block;
  margin-right: .84rem;
}
.main .main-title .title-right {
  font-size: 0.3rem;
  display: inline-block;
}
.main .main-title .title-bottom-border {
  width: .96rem;
  height: 0.06rem;
  margin: 0.15rem auto 0;
  background: #6c8bfe;
  border-radius: 0.03rem;
}
.main .main-title .title-click {
  font-family: PingFangSC-Semibold;
  color: #6c8bfe;
}
.main input {
  float: left;
  font-size: 0.3rem;
  font-family: PingFangSC-Regular;
  color: #2c3036;
  width: 6.54rem;
  height: .8rem;
  line-height: .8rem;
  border-bottom: .01rem solid #e5e5e5;
  padding-left: 2.14rem;
}
.main input::-ms-clear {
  display: none;
}
.main input::-ms-reveal {
  display: none;
}
.main input::placeholder {
  font-size: 0.3rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
}
.main .input-phone {
  margin-top: .4rem;
  overflow: hidden;
  position: relative;
}
.main .btnBefore {
  font-size: 0;
  position: absolute;
  top: .16rem;
  right: 0;
}
.main .btnBefore button {
  font-size: .28rem;
  font-family: PingFangSC-Regular;
  color: #6c8bfe;
  width: 1.31rem;
  height: .56rem;
  background: white;
  border: .02rem solid #6c8bfe;
  border-radius: .04rem;
}
.main .btnAfter {
  font-size: 0;
  position: absolute;
  top: .16rem;
  right: 0;
}
.main .btnAfter button {
  font-size: .28rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
  width: 1.31rem;
  height: .56rem;
  background: #f5f6f7;
  border-radius: .04rem;
}
.main .input-yanzheng2-img {
  width: 1.3rem;
  height: .5rem;
  background: url(../../../assets/img/yanzheng.png) no-repeat center;
  background-size: 1.3rem .5rem;
  position: absolute;
  top: .17rem;
  right: 0;
}
.main .input-yanzheng1,
.main .input-yanzheng2,
.main .input-psw1,
.main .input-psw2 {
  margin-top: .2rem;
  overflow: hidden;
  position: relative;
}
.main .input-phone:before,
.main .input-yanzheng1:before,
.main .input-yanzheng2:before,
.main .input-psw1:before,
.main .input-psw2:before {
  font-size: 0.3rem;
  font-family: PingFangSC-Regular;
  color: #707076;
  position: absolute;
  top: .25rem;
  left: 0;
}
.main .input-phone:before {
  content: "手机号";
}
.main .input-yanzheng1:before {
  content: "短信验证码";
}
.main .input-yanzheng2:before {
  content: "图形验证码";
}
.main .input-psw1:before {
  content: "设置密码";
}
.main .input-psw2:before {
  content: "再次输入密码";
}
.main .main-btn {
  overflow: hidden;
  font-size: 0;
  margin-top: .72rem;
  text-align: center;
}
.main .main-btn button {
  width: 4.6rem;
  height: .88rem;
  line-height: .88rem;
  background: #6c8bfe;
  box-shadow: 0px .04rem 0.12rem 0px rgba(108, 139, 254, 0.2);
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: white;
}
.main .text-wrapper {
  font-size: 0;
  text-align: center;
  margin: .44rem 0 .93rem;
}
.main .text-wrapper .cbLabel {
  display: inline-block;
  width: .24rem;
  height: .24rem;
  background: url(../../../assets/img/beforeClick.png) no-repeat center;
  background-size: .24rem .24rem;
  vertical-align: bottom;
  margin-right: .08rem;
}
.main .text-wrapper .cbLabel2 {
  display: inline-block;
  width: .24rem;
  height: .24rem;
  background: url(../../../assets/img/afterClick.png) no-repeat center;
  background-size: .24rem .24rem;
  vertical-align: bottom;
  margin-right: .08rem;
}
.main .text-wrapper .text {
  display: inline;
  font-size: .24rem;
  font-family: PingFangSC-Regular;
  color: #707076;
}
.main .text-wrapper .xieyi {
  margin-left: .04rem;
  color: #5668a6;
}
.main .main-btn {
  overflow: hidden;
  font-size: 0;
  margin-top: .72rem;
  text-align: center;
}
.main .main-btn button {
  width: 4.6rem;
  height: .88rem;
  line-height: .88rem;
  background: #6c8bfe;
  box-shadow: 0px .04rem 0.12rem 0px rgba(108, 139, 254, 0.2);
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: white;
}
.main .text-wrapper {
  font-size: 0;
  text-align: center;
  margin: .44rem 0 .93rem;
}
.main .text-wrapper .cbLabel {
  display: inline-block;
  width: .24rem;
  height: .24rem;
  background: url(../../../assets/img/beforeClick.png) no-repeat center;
  background-size: .24rem .24rem;
  vertical-align: bottom;
  margin-right: .08rem;
}
.main .text-wrapper .cbLabel2 {
  display: inline-block;
  width: .24rem;
  height: .24rem;
  background: url(../../../assets/img/afterClick.png) no-repeat center;
  background-size: .24rem .24rem;
  vertical-align: bottom;
  margin-right: .08rem;
}
.main .text-wrapper .text {
  display: inline;
  font-size: .24rem;
  font-family: PingFangSC-Regular;
  color: #707076;
}
.main .text-wrapper .xieyi {
  margin-left: .04rem;
  color: #5668a6;
}
</style>



