<template>
  <div class="body">
    <div class="main-container">
      <div class="input-psw">
        <span>设置密码</span>
        <input type="text" placeholder="请输入新登录密码" v-model="inputPsw">
      </div>
    </div>
    <div :class="{ mainBtn:isOk, btnHighlight:!isOk }">
      <router-link to="/login">
        <button :disabled="isOk">确定</button>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "ResetPswBody",
  data () {
    return {
      inputPsw: ''
    }
  },
  computed: {
    isOk () {
      if(this.inputPsw == '') {
        return true
      } else {
        return false
      }
    }
  }
};
</script>

<style scoped>
.main-container {
  background: white;
  padding-left: .3rem;
  font-size: 0;
}
.main-container span {
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #707076;
  line-height: .88rem;
}
.main-container input {
  float: right;
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #2c3036;
  width: 5.36rem;
  height: .88rem;
  line-height: .88rem;
  border-bottom: .01rem solid #e5e5e5;
}
.main-container input::-ms-clear {
  display: none;
}
.main-container input::-ms-reveal {
  display: none;
}
.main-container input::placeholder {
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
}
.mainBtn {
  font-size: 0;
  margin-top: .4rem;
  text-align: center;
}
.mainBtn button {
  width: 6.9rem;
  height: .96rem;
  background: #e5e5e5;
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: #979799;
}
.btnHighlight {
  font-size: 0;
  margin-top: .4rem;
  text-align: center;
}
.btnHighlight button {
  width: 6.9rem;
  height: .96rem;
  background: #6c8bfe;
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: #fff;
}
</style>




