<template>
  <div class="main">
    <home-header></home-header>
    <home-itemList></home-itemList>
    <home-jobCategory :list="categoryList"></home-jobCategory>
    <home-jobWanted :list="jobList"></home-jobWanted>
    <home-footer></home-footer>
  </div>
</template>

<script>
import axios from 'axios'
import HomeHeader from "./components/Header"
import HomeItemList from "./components/ItemList"
import HomeJobCategory from "./components/JobCategory"
import HomeJobWanted from "./components/JobWanted"
import HomeFooter from "./components/Footer"
export default {
  name: 'Home',
  components: {
    HomeHeader,
    HomeItemList,
    HomeJobCategory,
    HomeJobWanted,
    HomeFooter
  },
  data () {
    return {
      jobList: [],
      categoryList: []
    }
  },
  methods: {
    getHomeInfo () {
      axios.get('./static/mock/home.json')     //这里地址写的不规范
        .then(this.getHomeInfoSucc)
    },
    getHomeInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.jobList = data.jobList
        this.categoryList = data.categoryList
      }
    }
  },
  mounted () {
    this.getHomeInfo ()
  }
}
</script>

<style scoped>
  .main {
    background-color: #F5F6F7; 
  }
</style>




