<template>
  <div>
    <div class="main">
      <div class="input-entname">
        <input type="text" placeholder="请输入与营业执照一致名称" v-model="inputEntName">
      </div>
      <div class="input-people">
        <input type="text" placeholder="请输入2-10字符联系人名称" v-model="inputPeople">
      </div>
      <div class="input-tel">
        <input type="text" placeholder="请输入" v-model="inputTel">
      </div>
      <div class="input-phone">
        <input type="text" placeholder="请输入11位有效手机号" v-model="inputPhone">
      </div>
      <div class="input-name">
        <input type="text" placeholder="请输入登录用户名" v-model="inputName">
      </div>
      <div class="input-email">
        <input type="text" placeholder="请输入" v-model="inputEmail">
      </div>
      <div class="input-psw">
        <input type="password" placeholder="请输入" v-model="inputPsw">
      </div>
      <div class="input-psw2">
        <input type="password" placeholder="请输入" v-model="inputPsw2">
      </div>
      <div class="main-btn">
        <button @click="handleSubmit">立即注册</button>
      </div>
      <div class="text-wrapper">
        <div :class="{ cbLabel:iscbLabel, cbLabel2:iscbLabel2 }" @click="handleClick"></div>
        <p class="text">我已同意
          <span class="xieyi">E滁州人才网用户招聘协议</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "EntBody",
  data () {
    return {
      inputEntName: '',
      inputPeople: '',
      inputTel: '',
      inputPhone: '',
      inputName: '',
      inputEmail: '',
      inputPsw: '',
      inputPsw2: '',
      iscbLabel: true,
      iscbLabel2: false
    }
  },
  methods: {
    handleClick () {
      if (this.iscbLabel) {
        this.iscbLabel = !this.iscbLabel
        this.iscbLabel2 = !this.iscbLabel2
      } else {
        this.iscbLabel = !this.iscbLabel
        this.iscbLabel2 = !this.iscbLabel2 
      }
    },
    handleSubmit () {
      if (this.inputEntName == '' 
          || this.inputPeople == '' 
          || this.inputTel == '' 
          || this.inputPhone == '' 
          || this.inputName == '' 
          || this.inputEmail == '' 
          || this.inputPsw == ''
          || this.inputPsw2 == '') {
        alert('请填写完整信息')
      }
    }
  }
};
</script>

<style scoped>
.main {
  padding: 0 0.48rem;
}
.main input {
  float: left;
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #2c3036;
  width: 6.54rem;
  line-height: .8rem;
  height: .8rem;
  border-bottom: .01rem solid #e5e5e5;
  padding-left: 2.14rem;
}
.main input::-ms-clear {
  display: none;
}
.main input::-ms-reveal {
  display: none;
}
.main input::placeholder {
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
}
.main .input-entname {
  margin-top: .4rem;
  overflow: hidden;
  position: relative;
}
.main .input-people,
.main .input-tel,
.main .input-phone,
.main .input-name,
.main .input-email,
.main .input-psw,
.main .input-psw2 {
  margin-top: .2rem;
  overflow: hidden;
  position: relative;
}
.main .input-entname:before,
.main .input-people:before,
.main .input-tel:before,
.main .input-phone:before,
.main .input-name:before,
.main .input-email:before,
.main .input-psw:before,
.main .input-psw2:before {
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #707076;
  position: absolute;
  top: .25rem;
  left: 0;
}
.main .input-entname:before {
  content: "公司名称";
}
.main .input-people:before {
  content: "联系人";
}
.main .input-tel:before {
  content: "办公号码";
}
.main .input-phone:before {
  content: "手机号码";
}
.main .input-name:before {
  content: "用户名";
}
.main .input-email:before {
  content: "电子邮箱";
}
.main .input-psw:before {
  content: "设置密码";
}
.main .input-psw2:before {
  content: "再次输入密码";
}
.main .main-btn {
  overflow: hidden;
  font-size: 0;
  margin-top: .72rem;
  text-align: center;
}
.main .main-btn button {
  width: 4.6rem;
  height: .88rem;
  line-height: .88rem;
  background: #6c8bfe;
  box-shadow: 0px .04rem 0.12rem 0px rgba(108, 139, 254, 0.2);
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: white;
}
.main .text-wrapper {
  font-size: 0;
  text-align: center;
  margin: .44rem 0 .93rem;
}
.main .text-wrapper .cbLabel {
  display: inline-block;
  width: .24rem;
  height: .24rem;
  background: url(../../../assets/img/beforeClick.png) no-repeat center;
  background-size: .24rem .24rem;
  vertical-align: bottom;
  margin-right: .08rem;
}
.main .text-wrapper .cbLabel2 {
  display: inline-block;
  width: .24rem;
  height: .24rem;
  background: url(../../../assets/img/afterClick.png) no-repeat center;
  background-size: .24rem .24rem;
  vertical-align: bottom;
  margin-right: .08rem;
}
.main .text-wrapper .text {
  display: inline;
  font-size: .24rem;
  font-family: PingFangSC-Regular;
  color: #707076;
}
.main .text-wrapper .xieyi {
  margin-left: .04rem;
  color: #5668a6;
}
</style>



