<template>
  <div class="main">
    <div class="main-title"><img src="../../../assets/img/img_title_job.png" alt=""></div>
    <div class="item-list">
      <ul>
        <li class="item"
        v-for="item of list"
        :key="item.id"
        v-if="item.id <= num"
        >{{item.categoryName}}</li>
      </ul>
    </div>
    <div class="list-icon-wrapper">
      <div class="icon-wrapper" @click="handleClick">
        <div class="list-icon-text">展开全部</div>
        <div class="icon"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeJobCategory',
  data () {
    return {
      num: 16,
      isShow: true
    }
  },
  props: {
    list: Array
  },
  methods: {
    handleClick () {
      if (this.isShow) {
        this.num = Infinity     //表示无穷大
        this.isShow = !this.isShow
      } else {
        this.num = 16
        this.isShow = !this.isShow
      }
    }
  }
};
</script>

<style scoped>
.main{
  text-align: center;
  background: #fff;
  overflow: hidden;
  margin-bottom: .2rem;
}
.main-title {
  margin-top: .4rem;
  margin-bottom: .36rem;
}
.main-title img {
  width: 2.68rem;
  height: .35rem;
}
.item-list {
  margin-left: .3rem;
  overflow: hidden;
}
.item-list ul {
  overflow: hidden;
}
.item-list ul li {
  float: left;
  margin-right: .16rem;
  margin-bottom: .16rem;
  box-sizing: border-box;
}
.item-list .item {
  font-size:.28rem;
  font-family:PingFangSC-Regular;
  color:rgba(112,112,118,1);
  width: 1.6rem;
  height: .62rem;
  line-height: .62rem;
  background:rgba(245,246,247,1);
  border-radius:.04rem;
}
.list-icon-wrapper {
  font-size: 0;
  margin-top: .26rem;
  margin-bottom: .46rem;
}
.icon-wrapper {
  overflow: hidden;
  display: inline-block;
}
.list-icon-text {
  display: inline;
  font-size:.28rem;
  font-family:PingFangSC-Regular;
  color:rgba(86,104,166,1);
}
.list-icon-wrapper .icon {
  display: inline-block;
  width: .24rem;
  height: .28rem;
  background: url(../../../assets/img/cut_ic_clickmore.png) no-repeat center;
  background-size: .24rem .24rem;
  margin-left: .08rem;
  vertical-align: bottom;
}
</style>




