<template>
  <div class="header">
    <div class="header-title">
      <div class="header-logo">
        <img src="../../../assets/img/logo.png" alt="user">
      </div>
      <div class="header-user">
        <router-link to="/login">
          <img src="../../../assets/img/img_user.png" alt="user">
        </router-link>
      </div>
    </div>
    <div class="header-input">
      <div class="icon"></div>
      <div class="text">搜索职位/企业</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeHeader'
};
</script>

<style scoped>
.header {
  height:2.32rem;
  background:linear-gradient(-90deg,rgba(108,139,254,1),rgba(106,158,255,1)); 
  overflow: hidden;
}
.header-title {
  margin-top: .36rem;
  position: relative;
}
.header-logo {
  text-align: center;
}
.header-logo img {
  width: 3.46rem;
  height: .64rem;
}
.header-user img {
  width: .72rem;
  height: .72rem;
  position: absolute;
  top: -.04rem;
  right: .3rem;
}
.header-input {
  width:6.9rem;
  height:.72rem;
  background:rgba(255,255,255,1);
  box-shadow:0 .02rem .12rem 0 rgba(44,48,54,0.1);
  border-radius:.06rem;
  text-align: center;
  margin: .28rem auto 0;
  font-size: 0;
}
.header-input .icon {
  display: inline-block;
  width: .3rem;
  height: .3rem;
  background: url(../../../assets/img/cut_ic_search.png) no-repeat center;
  background-size: .3rem .3rem;
  vertical-align: bottom;
  margin-top: .2rem;
  margin-right: .16rem;
}
.header-input .text {
  display: inline;
  font-size:.3rem;
  font-family:PingFangSC-Regular;
  color:rgba(151,151,153,1);
}
</style>




