<template>
  <div>
    <div class="main">
      <div class="input-name">
        <input type="text" placeholder="输入用户名/手机号码" v-model="inputName">
      </div>
      <div class="input-psw">
        <input type="password" placeholder="输入登录密码" v-model="inputPsw">
        <div id="eye" @click="handleClick"><img :src="eyeSrc" alt="eye" class="eyesclose-img"></div>
      </div>
      <router-link to="/findPsw">
        <div class="forgetPsw">忘记密码</div>
      </router-link>
      <div class="main-btn">
        <button @click="handleSubmit">登录</button>
      </div>
      <div class="main-footer">
        <span class="line"></span>
        <span class="text">使用第三方登录</span>
        <span class="line"></span>
      </div>
      <div class="wechat"><img src="../../../assets/img/wechat.png" alt="wechat"></div>
    </div>
  </div>
</template>

<script>
import eyesClose from "@/assets/img/eyesclose.png"
import eyesOpen from "@/assets/img/eyesopen.png"
export default {
  name: "LoginBody",
  data () {
    return {
      inputName: '',
      inputPsw: '',
      eyeSrc: eyesClose,
      eyeOpen: true
    }
  },
  methods: {
    handleClick () {
      if (this.eyeOpen) {
        this.eyeSrc = eyesOpen
        this.eyeOpen = !this.eyeOpen
      } else {
        this.eyeSrc = eyesClose
        this.eyeOpen = !this.eyeOpen
      }
    },
    handleSubmit () {
      if (this.inputName == '' || this.inputPsw == '') {
        alert('账号或密码不能为空！')
      }
    }
  }
};
</script>

<style scoped>
.main {
  border-radius: .08rem .08rem 0 0;
  padding: .64rem .48rem 0 .48rem;
  position: relative;
  top: -0.04rem;
  background: #fff;
  /* 去除姓名框内最右侧大黑叉 */
  /* 去除密码框内最右侧大眼睛 */
}
.main input {
  float: left;
  font-size: 0.3rem;
  font-family: PingFangSC-Medium;
  color: #2c3036;
  width: 6.54rem;
  height: .8rem;
  line-height: .8rem;
  border-bottom: .01rem solid #e5e5e5;
  padding-left: 0.6rem;
}
.main input::-ms-clear {
  display: none;
}
.main input::-ms-reveal {
  display: none;
}
.main input::placeholder {
  line-height: .8rem;
  font-size: 0.3rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
}
.main .input-name {
  overflow: hidden;
  margin-bottom: 0.2rem;
  position: relative;
}
.main .input-name:before {
  content: "";
  width: .34rem;
  height: .36rem;
  background: url(../../../assets/img/user.png) no-repeat center;
  background-size: .34rem 0.36rem;
  position: absolute;
  top: .2rem;
  left: .05rem;
}
.main .input-psw {
  overflow: hidden;
  position: relative;
}
.main .input-psw:before {
  content: "";
  width: .32rem;
  height: .36rem;
  background: url(../../../assets/img/psw.png) no-repeat center;
  background-size: .32rem .36rem;
  position: absolute;
  top: .2rem;
  left: .06rem;
}
.main #eye {
  font-size: 0;
  width: .35rem;
  height: .3rem;
  cursor: pointer;
  position: absolute;
  bottom: .25rem;
  right: .05rem;
}
.main #eye .eyesclose-img {
  width: .35rem;
  height: .3rem;
}
.main #eye .eyesopen-img {
  width: .35rem;
  height: .24rem;
  position: relative;
  top: 0.03rem;
}
.main .forgetPsw {
  font-size: 0.3rem;
  font-family: PingFangSC-Regular;
  color: #5668a6;
  line-height: .42rem;
  margin-top: .32rem;
}
.main .main-btn {
  text-align: center;
  font-size: 0;
  margin-top: .72rem;
}
.main .main-btn button {
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: white;
  width: 4.6rem;
  height: .88rem;
  background: #6c8bfe;
  box-shadow: 0 .04rem 0.12rem 0 rgba(108, 139, 254, 0.2);
  border-radius: .08rem;
}
.main .main-footer {
  font-size: 0;
  text-align: center;
  margin-top: 2.6rem;
}
.main .main-footer .line {
  display: inline-block;
  width: 2rem;
  height: .01rem;
  background: #e5e5e5;
  vertical-align: middle;
}
.main .main-footer .text {
  font-size: .24rem;
  font-family: PingFangSC-Regular;
  color: #979799;
  margin: 0 .24rem;
  vertical-align: middle;
}
.main .wechat {
  margin: .24rem auto .47rem;
  text-align: center;
  line-height: 0;
}
.main .wechat img {
  width: .88rem;
  height: .88rem;
}
</style>



