<template>
  <div class="body">
    <div class="main-container">
      <div class="input-phone">
        <input type="text" placeholder="请输入登陆手机号" v-model="inputPhone">
      </div>
      <div class="input-yanzheng">
        <input type="text" placeholder="请输入验证码" v-model="inputYanZheng">
        <div :class="{ btnBefore:obj.isBtnBefore, btnAfter:obj.isBtnAfter }">
          <button @click="handleClick" :disabled="obj.isClick">{{obj.btInfo}}</button>
        </div>
      </div>
    </div>
    <div :class="{ mainBtn:isOk, btnHighlight:!isOk }">
      <router-link to="/resetPsw">
        <button :disabled="isOk">马上验证</button>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "FindPswBody",
  data () {
    return {
      inputPhone: '',
      inputYanZheng: '',
      obj: {
        btInfo: '获取',
        isClick: false,
        isBtnBefore: true,
        isBtnAfter: false
      }
    }
  },
  computed: {
    isOk () {
      if(this.inputPhone == '' || this.inputYanZheng == '') {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    handleClick () {
      var obj = this.obj
      var countdown = 60 
		  function settime(obj) {
			  if(countdown == 0) {
				  obj.btInfo  = '获取'
          obj.isClick = false
          obj.isBtnBefore = true
          obj.isBtnAfter = false
				  countdown = 60
				  clearInterval(timer)
				  return;
			  } else {
          obj.isBtnBefore = false
          obj.isBtnAfter = true
          obj.isClick = true
          obj.btInfo = countdown + 's'
          countdown--
        }
		  };
		  var timer = setInterval(function() {
			  settime(obj)
		  }, 1000) 
    }
  }
};
</script>

<style scoped>
.main-container {
  background: white;
  padding-left: 0.3rem;
  font-size: 0;
}
.main-container input {
  float: right;
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #2c3036;
  width: 5.36rem;
  height: .88rem;
  line-height: .88rem;
  border-bottom: .01rem solid #e5e5e5;
}
.main-container .input-phone:before {
  content: "手机号";
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #707076;
  position: relative;
  top: .29rem;
}
.main-container .input-yanzheng:before {
  content: "短信验证码";
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #707076;
  position: relative;
  top: .29rem;
}
.main-container input::-ms-clear {
  display: none;
}
.main-container input::-ms-reveal {
  display: none;
}
.main-container input::placeholder {
  font-size: .3rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
}
.main-container .input-phone,
.main-container .input-yanzheng {
  overflow: hidden;
}
.main-container .input-yanzheng {
  position: relative;
}
.btnBefore {
  font-size: 0;
  position: absolute;
  top: .16rem;
  right: .3rem;
}
.btnBefore button {
  font-size: .28rem;
  font-family: PingFangSC-Regular;
  color: #6c8bfe;
  width: 1.31rem;
  height: .56rem;
  background: white;
  border: .02rem solid #6c8bfe;
  border-radius: .04rem;
}
.btnAfter {
  font-size: 0;
  position: absolute;
  top: .16rem;
  right: .3rem;
}
.btnAfter button {
  font-size: .28rem;
  font-family: PingFangSC-Regular;
  color: #c6c6c8;
  width: 1.31rem;
  height: .56rem;
  background: #f5f6f7;
  border-radius: .04rem;
}
.mainBtn {
  font-size: 0;
  margin-top: .4rem;
  text-align: center;
}
.mainBtn button {
  width: 6.9rem;
  height: .96rem;
  background: #e5e5e5;
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: #979799;
}
.btnHighlight {
  font-size: 0;
  margin-top: .4rem;
  text-align: center;
}
.btnHighlight button {
  width: 6.9rem;
  height: .96rem;
  background: #6c8bfe;
  border-radius: .08rem;
  font-size: .36rem;
  font-family: PingFangSC-Semibold;
  color: #fff;
}
</style>




