<template>
  <div>
    <div class="banner">
      <router-link to="/home">
        <div class="img-close"><img src="../../../assets/img/close.png" alt="close"></div>
      </router-link>
      <div class="img-logo"><img src="../../../assets/img/logo.png" alt="logo"></div>
      <div class="banner-btn">
        <router-link to="/login">
          <button id="btn-left">登录</button>
        </router-link>
        <button id="btn-right" class="btn-click">注册</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginHeader"
};
</script>

<style scoped>
.banner {
  width: 100%;
  height: 2.8rem;
  line-height: 0;
  background: linear-gradient(-90deg, #6c8bfe, #6a9eff);
  overflow: hidden;
}
.banner .img-close {
  width: .44rem;
  height: .44rem;
  margin-top: .32rem;
  margin-left: .32rem;
}
.banner .img-close img {
  width: .44rem;
  height: .44rem;
}
.banner .img-logo {
  text-align: center;
}
.banner .img-logo img {
  width: 3.46rem;
  height: .64rem;
}
.banner .banner-btn {
  text-align: center;
  margin-top: .32rem;
  font-size: 0;
}
.banner .banner-btn button {
  font-size: 0.3rem;
  font-family: PingFangSC-Regular;
  color: #707076;
  width: 1.38rem;
  height: .64rem;
  background: white;
  border: .01rem solid #e5e5e5;
}
.banner .banner-btn .btn-click {
  font-family: PingFangSC-Regular;
  color: #6c8bfe;
}
.banner .banner-btn #btn-left {
  border-radius: .04rem 0 0 .04rem;
}
.banner .banner-btn #btn-right {
  border-radius: 0 .04rem .04rem 0;
}
</style>



